import React, { useContext, useEffect, useState } from "react";
import { Switch } from "@mui/material";
import { AccountContext } from "../context/accountContext";
import { UserContext } from "../context/userContext";
import { url } from "../service";
export function AccountPopbox({ onclose, editInfo }) {
  const { accounts, setAccounts } = useContext(AccountContext);
  const { user, token, setMessage } = useContext(UserContext);
  const [newAccount, setNewAccount] = useState({
    _id: null,
    userId: user._id,
    initialAmount: 0,
    accountsType: "",
    toAccountId: "",
    autoDebit: false,
    autoDebitDay: "",
  });
  useEffect(() => {
    if (editInfo) {
      setNewAccount(editInfo);
    } else {
      setNewAccount({
        _id: null,
        userId: user._id,
        initialAmount: 0,
        accountsType: "",
        toAccountId: "",
        autoDebit: false,
        autoDebitDay: "",
      });
    }
  }, [editInfo, user._id]);
  const handleSubmit = async () => {
    if (newAccount._id) {
      await updateAccount();
    } else {
      await addAccount();
    }
  };
  const handleChange = (e) => {
    const { name, value } = e.target;
    setNewAccount((prev) => ({
      ...prev,
      [name]: value,
    }));
  };
  const addAccount = async () => {
    try {
      const res = await fetch(`${url}/accounts/add`, {
        method: "post",
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-Type": "application/json",
        },
        body: JSON.stringify(newAccount),
      });

      const data = await res.json();

      if (data.ok) {
        setAccounts((prev) => [...prev, data.account]);
        setMessage({ status: "success", text: "", open: true });
        onclose();
      } else {
        setMessage({ status: "warning", text: data.error, open: true });
      }
      setNewAccount({
        _id: null,
        userId: user._id,
        initialAmount: 0,
        accountsType: "",
        toAccountId: "",
        autoDebit: false,
        autoDebitDay: "",
      });
    } catch (error) {
      console.log(error);
      setMessage({ status: "error", text: "", open: true });
    }
  };
  const updateAccount = async () => {
    try {
      const res = await fetch(`${url}/accounts/update`, {
        method: "post",
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-Type": "application/json",
        },
        body: JSON.stringify(newAccount),
      });
      const data = await res.json();
      if (data.ok) {
        setAccounts((prev) => {
          let newPrev = prev?.map((item) => {
            if (item._id === newAccount._id) {
              return data.account;
            }
            return item;
          });
          console.log(newPrev);
          return newPrev;
        });
        setMessage({ status: "success", text: "", open: true });
        onclose();
      } else {
        setMessage({ status: "warning", text: data.error, open: true });
      }
    } catch (error) {
      setMessage({ status: "error", text: "", open: true });
      console.log(error);
    }
  };
  console.log(newAccount);
  const days = Array.from({ length: 31 }, (_, i) => i + 1);
  return (
    <div className="popbox">
      <div className="account-pop"></div>
      <p>{newAccount._id ? "" : ""}</p>
      <div className="Finances-put">
        <p></p>
        <input
          type="text"
          onChange={(e) => handleChange(e)}
          placeholder=""
          name="accountsType"
          value={newAccount.accountsType}
        />
      </div>
      <div className="Finances-put">
        <p></p>
        <input
          type="number"
          name="initialAmount"
          placeholder="$"
          inputMode="numeric"
          onChange={(e) => handleChange(e)}
          value={newAccount.initialAmount}
        />
      </div>
      <div className="Finances-put">
        <p></p>
        <Switch
          name="autoDebit"
          checked={newAccount.autoDebit}
          onChange={(e) =>
            setNewAccount((prev) => ({ ...prev, autoDebit: e.target.checked }))
          }
        />
      </div>
      <div className="Finances-put">
        <p></p>
        <select
          value={newAccount.toAccountId}
          disabled={!newAccount?.autoDebit}
          onChange={(e) =>
            setNewAccount((prev) => ({ ...prev, toAccountId: e.target.value }))
          }
        >
          <option selected hidden value="">
            ----
          </option>
          {accounts &&
            accounts.map((item) => (
              <option key={item._id} value={item._id}>
                {item.accountsType}
              </option>
            ))}
        </select>
      </div>
      <div className="Finances-put">
        <p></p>
        <select
          disabled={!newAccount?.autoDebit}
          value={newAccount.autoDebitDay}
          onChange={(e) =>
            setNewAccount((prev) => ({ ...prev, autoDebitDay: e.target.value }))
          }
        >
          <option value=""></option>
          {days.map((day) => (
            <option key={day} value={day}>
              {day}
            </option>
          ))}
        </select>
      </div>
      <div className="pop-btn">
        <button
          onClick={() => {
            onclose();
          }}
        >
          
        </button>
        <button
          onClick={() => {
            handleSubmit();
          }}
        >
          
        </button>
      </div>
    </div>
  );
}
export function CategroyPopbox({ onclose, editInfo }) {
  const { setCategories } = useContext(AccountContext);
  const { token, user, setMessage } = useContext(UserContext);
  const [newCategory, setNewCategory] = useState({
    _id: null,
    userId: user._id,
    categoriesType: "",
    source: "",
  });
  useEffect(() => {
    if (editInfo) {
      setNewCategory(editInfo);
    } else {
      setNewCategory({
        _id: null,
        userId: user._id,
        categoriesType: "",
        source: "",
      });
    }
  }, [editInfo, user]);
  const handleSubmit = async () => {
    if (newCategory._id) {
      await updateCategory();
    } else {
      await addCategory();
    }
  };
  const handleChange = (e) => {
    const { name, value } = e.target;
    setNewCategory((prev) => ({
      ...prev,
      [name]: value,
    }));
  };
  const updateCategory = async () => {
    try {
      const res = await fetch(`${url}/categories/update`, {
        method: "post",
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-Type": "application/json",
        },
        body: JSON.stringify(newCategory),
      });
      const data = await res.json();
      if (data.ok) {
        setCategories((prev) => {
          let newPrev = prev?.find((item) => item._id === newCategory._id);
          newPrev.categoriesType = data.category.categoriesType;
          return prev;
        });
        onclose();
        setMessage({ status: "success", text: "", open: true });
      } else {
        setMessage({ status: "warning", text: "", open: true });
      }
    } catch (error) {
      console.log(error);
      setMessage({ status: "error", text: "", open: true });
    }
  };
  async function addCategory() {
    try {
      const res = await fetch(`${url}/categories/add`, {
        method: "post",
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-Type": "application/json",
        },
        body: JSON.stringify(newCategory),
      });
      const data = await res.json();
      if (data.ok) {
        setCategories((prev) => [...prev, data.category]);
        setMessage({ status: "success", text: "", open: true });
        onclose();
      } else {
        setMessage({ status: "warning", text: data.error, open: true });
      }
    } catch (error) {
      console.log(error);
      setMessage({ status: "error", text: "", open: true });
    }
  }
  return (
    <div className="popbox">
      <div className="account-pop"></div>
      <p>{newCategory._id ? "" : ""}</p>
      <div className="Finances-put">
        <p></p>
        <input
          type="text"
          onChange={(e) => handleChange(e)}
          placeholder=""
          name="categoriesType"
          value={newCategory.categoriesType}
        />
      </div>
      <div className="Finances-put">
        <p></p>
        <select
          name="source"
          value={newCategory.source}
          onChange={(e) =>
            setNewCategory((prev) => ({ ...prev, source: e.target.value }))
          }
        >
          <option value="" hidden>
            ----
          </option>
          <option value="income"></option>
          <option value="expense"></option>
          <option value="change"></option>
        </select>
      </div>

      <div className="pop-btn">
        <button
          onClick={() => {
            onclose();
          }}
        >
          
        </button>
        <button
          onClick={() => {
            handleSubmit();
          }}
        >
          
        </button>
      </div>
    </div>
  );
}
